import { dinnerRecipes } from "@/lib/data/dinners";

export default function CenasPage() {
  return (
    <div className='container py-6'>
      <h1 className='text-3xl font-bold mb-6'>Opciones de Cena</h1>
      <div className='grid gap-6'>
        {dinnerRecipes.map((recipe) => (
          <article
            key={recipe.id}
            className='p-6 bg-card text-card-foreground rounded-lg border'
          >
            <h2 className='text-2xl font-semibold mb-4'>{recipe.title}</h2>

            {/* Tiempo y Porciones */}
            <div className='flex gap-4 text-sm text-muted-foreground mb-6'>
              {recipe.preparationTime && (
                <p>‚è±Ô∏è Tiempo: {recipe.preparationTime}</p>
              )}
              {recipe.servingSize && <p>üçΩÔ∏è Rinde: {recipe.servingSize}</p>}
            </div>

            {/* Ingredientes */}
            <div className='mb-6'>
              <h3 className='font-semibold mb-2'>Ingredientes:</h3>
              <ul className='list-disc pl-5 space-y-1'>
                {recipe.ingredients.map((ingredient, index) => (
                  <li key={index}>{ingredient}</li>
                ))}
              </ul>
            </div>

            {/* Instrucciones */}
            <div className='mb-6'>
              <h3 className='font-semibold mb-2'>Preparaci√≥n:</h3>
              <ol className='list-decimal pl-5 space-y-1'>
                {recipe.instructions.map((step, index) => (
                  <li key={index}>{step}</li>
                ))}
              </ol>
            </div>

            {/* Consejos de Cocci√≥n */}
            {recipe.cookingTips && (
              <div className='mb-6'>
                <h3 className='font-semibold mb-2'>Consejos de Cocci√≥n:</h3>
                <ul className='list-disc pl-5 space-y-1'>
                  {recipe.cookingTips.map((tip, index) => (
                    <li key={index}>{tip}</li>
                  ))}
                </ul>
              </div>
            )}

            {/* Beneficios */}
            <div className='mb-6'>
              <h3 className='font-semibold mb-2'>Beneficios:</h3>
              <ul className='list-disc pl-5 space-y-1'>
                {recipe.benefits.map((benefit, index) => (
                  <li key={index}>{benefit}</li>
                ))}
              </ul>
            </div>

            {/* Etiquetas */}
            {recipe.healthGoals && (
              <div className='flex gap-2'>
                {recipe.healthGoals.map((goal) => (
                  <span
                    key={goal}
                    className='px-2 py-1 bg-primary/10 text-primary text-sm rounded-full'
                  >
                    {goal === "lowCholesterol"
                      ? "Bajo en Colesterol"
                      : goal === "immuneSystem"
                        ? "Sistema Inmune"
                        : "Equilibrado"}
                  </span>
                ))}
              </div>
            )}
          </article>
        ))}
      </div>
    </div>
  );
}
